import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { User, AuthState } from '@/types';
import { authService } from '@/services/authService';

interface AuthStore extends AuthState {
  // Actions
  setUser: (user: User | null) => void;
  setLoading: (loading: boolean) => void;
  login: (user: User) => void;
  logout: () => void;
  updateUser: (updates: Partial<User>) => void;
  addTokens: (amount: number) => void;
  consumeTokens: (amount: number) => boolean;
  loginWithGoogle: (googleToken: string) => Promise<void>;
  validateSession: () => Promise<void>;
  refreshTokens: () => Promise<void>;
}

export const useAuthStore = create<AuthStore>()(
  persist(
    (set, get) => ({
      user: null,
      isLoading: false,
      isAuthenticated: false,

      setUser: (user) => set({ 
        user, 
        isAuthenticated: !!user 
      }),

      setLoading: (isLoading) => set({ isLoading }),

      login: (user) => set({ 
        user, 
        isAuthenticated: true, 
        isLoading: false 
      }),

      logout: () => {
        localStorage.removeItem('auth-token');
        localStorage.removeItem('user-data');
        set({ 
          user: null, 
          isAuthenticated: false, 
          isLoading: false 
        });
      },

      updateUser: (updates) => {
        const currentUser = get().user;
        if (currentUser) {
          set({ 
            user: { ...currentUser, ...updates } 
          });
        }
      },

      addTokens: (amount) => {
        const currentUser = get().user;
        if (currentUser) {
          set({ 
            user: { 
              ...currentUser, 
              tokens: (currentUser.tokens || 0) + amount 
            } 
          });
        }
      },

      consumeTokens: (amount) => {
        const currentUser = get().user;
        if (currentUser && (currentUser.tokens || 0) >= amount) {
          // Optimistically update UI
          set({ 
            user: { 
              ...currentUser, 
              tokens: (currentUser.tokens || 0) - amount 
            } 
          });
          
          // Fetch actual token count from API
          get().refreshTokens();
          
          return true;
        }
        return false;
      },

      loginWithGoogle: async (googleToken) => {
        set({ isLoading: true });
        try {
          const response = await authService.loginWithGoogle(googleToken);
          
          // Guardar token en localStorage
          localStorage.setItem('auth-token', response.token);
          
          // Guardar datos del usuario en localStorage para validacin posterior
          localStorage.setItem('user-data', JSON.stringify(response.user));
          
          set({ 
            user: response.user, 
            isAuthenticated: true, 
            isLoading: false 
          });

          // Redireccin basada en si el usuario tiene CVs
          // Esta lgica se manejar en el componente que llama a loginWithGoogle
          // para evitar dependencias circulares
        } catch (error) {
          console.error('Error en loginWithGoogle:', error);
          set({ isLoading: false });
          throw error;
        }
      },

      validateSession: async () => {
        const token = localStorage.getItem('auth-token');
        if (!token) return;

        try {
          const user = await authService.getUserFromToken(token);
          if (user) {
            set({ user, isAuthenticated: true });
          } else {
            set({ user: null, isAuthenticated: false });
            localStorage.removeItem('auth-token');
            localStorage.removeItem('user-data');
          }
        } catch (error) {
          console.error('Error validando sesin:', error);
          set({ user: null, isAuthenticated: false });
          localStorage.removeItem('auth-token');
          localStorage.removeItem('user-data');
        }
      },

      refreshTokens: async () => {
        const token = localStorage.getItem('auth-token');
        const currentUser = get().user;
        
        if (!token || !currentUser) return;

        try {
          const tokens = await authService.getUserTokens(token);
          set({ 
            user: { 
              ...currentUser, 
              tokens: tokens 
            } 
          });
        } catch (error) {
          console.error('Error refreshing tokens:', error);
          // Don't throw, just log - this is a background refresh
        }
      },
    }),
    {
      name: 'auth-storage',
      partialize: (state) => ({ 
        user: state.user, 
        isAuthenticated: state.isAuthenticated 
      }),
    }
  )
);
