// User and Authentication Types
export interface User {
  id: string;
  email: string;
  firstName: string;
  lastName: string;
  fullName?: string; // Mantener para compatibilidad
  avatarUrl?: string;
  city?: string;
  country?: string;
  location?: string; // Mantener para compatibilidad
  linkedin?: string;
  targetFunction?: string;
  profession?: string; // Nuevo campo para profesin
  provider: 'google' | 'facebook' | 'linkedin';
  isPremium: boolean;
  tokens?: number; // Optional since we have separate API for tokens
  createdAt: Date;
  updatedAt: Date;
}

export interface AuthState {
  user: User | null;
  isLoading: boolean;
  isAuthenticated: boolean;
}

// Resume Data Types
export interface ResumeData {
  // Step 1: Professional Profile
  firstName: string;
  lastName: string;
  country: string;
  linkedin: string;
  language: 'es' | 'en';
  targetLevel: 'entry' | 'mid' | 'senior' | 'executive';
  profession: string;
  tone: 'professional' | 'creative' | 'technical' | 'friendly';
  phone: string;
  email: string;
  
  // Step 2: Skills
  skillsRaw: string[];
  toolsRaw: string[];
  
  // Step 3: Work Experience
  experience: WorkExperience[];
  
  // Step 4: Education and Certifications
  education: Education[];
  certifications: Certification[];
  
  // Step 5: Projects and Languages
  projects: Project[];
  languages: Language[];
  
  // Step 6: Key Achievements
  achievements: Achievement[];
  
  // Step 7: Professional Summary
  summary: string;
  jobDescription: string;
  
  // Metadata
  completedSteps: number[];
  currentStep: number;
  totalCharacters: number;
  lastSaved: Date;
}

export interface WorkExperience {
  id: string;
  title: string;
  company: string;
  startDate: string;
  endDate?: string;
  isCurrent: boolean;
  achievements: string[];
  responsibilities: string[];
}

export interface Education {
  id: string;
  institution: string;
  degree: string;
  field: string;
  startDate: string;
  endDate?: string;
  isCompleted: boolean;
  gpa?: string;
}

export interface Certification {
  id: string;
  name: string;
  issuer: string;
  date: string;
  credentialId?: string;
  url?: string;
}

export interface Project {
  id: string;
  name: string;
  description: string;
  technologies: string[];
  url?: string;
  startDate: string;
  endDate?: string;
  isOngoing: boolean;
}

export interface Language {
  id: string;
  name: string;
  level: 'basic' | 'intermediate' | 'advanced' | 'native';
}

export interface Achievement {
  id: string;
  title: string;
  description: string;
  year: string;
  _suggestionTitle?: string; // Metadata para rastrear sugerencia original
}

// Wizard Types
export interface WizardStep {
  id: number;
  title: string;
  description: string;
  isCompleted: boolean;
  isSkippable: boolean;
}

export interface WizardState {
  currentStep: number;
  completedSteps: number[];
  mode: 'manual' | 'guided';
  isCompleted: boolean;
}

// Premium and Monetization Types
export interface TokenPackage {
  id: string;
  name: string;
  tokens: number;
  price: number;
  currency: string;
  description: string;
  isPopular?: boolean;
  isLifetime?: boolean;
}

export interface PremiumFeature {
  id: string;
  name: string;
  description: string;
  tokenCost: number;
  isAvailable: boolean;
}

// Translation Types
export interface TranslationOptions {
  targetLanguage: 'en' | 'es' | 'ar' | 'zh' | 'hi';
  preserveFormat: boolean;
  professionalTone: boolean;
}

export interface TranslationResult {
  originalText: string;
  translatedText: string;
  language: string;
  tokensUsed: number;
  timestamp: Date;
}

// UI and Component Types
export interface HUDData {
  currentStep: number;
  totalSteps: number;
  charactersUsed: number;
  maxCharacters: number;
  progressPercentage: number;
  isNearLimit: boolean;
}

// API Response Types
export interface ApiResponse<T = any> {
  success: boolean;
  data?: T;
  error?: string;
  message?: string;
}

export interface PaginatedResponse<T> extends ApiResponse<T[]> {
  pagination: {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
  };
}

// Form Types
export interface FormField {
  name: string;
  label: string;
  type: 'text' | 'email' | 'textarea' | 'select' | 'multiselect' | 'date' | 'checkbox';
  required: boolean;
  placeholder?: string;
  options?: { value: string; label: string }[];
  validation?: {
    minLength?: number;
    maxLength?: number;
    pattern?: RegExp;
    custom?: (value: any) => string | undefined;
  };
}

// Error Types
export interface AppError {
  code: string;
  message: string;
  details?: any;
  timestamp: Date;
}

// AI Generated Resume Types
export interface GeneratedResume {
  professionalSummary: string;
  experience: EnhancedExperience[];
  education: EnhancedEducation[];
  skills: {
    technical: string[];
    soft: string[];
    tools: string[];
  };
  projects: EnhancedProject[];
  certifications: EnhancedCertification[];
  achievements: string[];
  languages: LanguageProficiency[];
  contactInfo: {
    fullName: string;
    email: string;
    phone: string;
    location: string;
    linkedin?: string;
  };
  metadata: {
    generatedAt: string;
    tokensUsed: number;
    aiProvider: string;
    model: string;
  };
}

export interface EnhancedExperience {
  title: string;
  company: string;
  duration: string;
  location?: string;
  description: string;
  achievements: string[];
  skills: string[];
  impact: string[];
}

export interface EnhancedEducation {
  degree: string;
  institution: string;
  field: string;
  duration: string;
  gpa?: string;
  relevantCoursework?: string[];
  honors?: string[];
}

export interface EnhancedProject {
  name: string;
  description: string;
  technologies: string[];
  duration: string;
  url?: string;
  achievements: string[];
  impact: string;
}

export interface EnhancedCertification {
  name: string;
  issuer: string;
  date: string;
  credentialId?: string;
  url?: string;
  skills: string[];
}

export interface LanguageProficiency {
  language: string;
  level: string;
  certifications?: string[];
}

// API Request/Response Types
export interface GenerateResumeRequest {
  resumeData: ResumeData;
  resumeId?: string;
}

export interface GenerateResumeResponse {
  success: boolean;
  data?: GeneratedResume;
  error?: string;
  message?: string;
  tokensUsed?: number;
  resumeId?: string;
}

// Resume Management Types
export interface Resume {
  id: string;
  userId: string;
  title: string;
  resumeData: ResumeData;
  generatedResume?: GeneratedResume;
  status: 'draft' | 'generated' | 'optimized';
  createdAt: Date;
  updatedAt: Date;
}

export interface JobInterest {
  id: string;
  userId: string;
  jobTitle: string;
  company: string;
  jobDescription: string;
  jobUrl?: string;
  optimizedResumeId?: string;
  status: 'active' | 'applied' | 'closed';
  createdAt: Date;
}

export interface JobInterestData {
  jobTitle: string;
  company: string;
  jobDescription: string;
  jobUrl?: string;
}

// Dashboard Types
export interface DashboardStats {
  totalResumes: number;
  totalJobInterests: number;
  tokensAvailable: number;
}

export interface DashboardState {
  resumes: Resume[];
  jobInterests: JobInterest[];
  stats: DashboardStats;
  isLoading: boolean;
}

// Profession Suggestions Types
export interface ProfessionSuggestions {
  skills: string[];
  tools: string[];
}

export interface SuggestionsResponse {
  success: boolean;
  data?: ProfessionSuggestions;
  fromCache: boolean;
  error?: string;
  message?: string;
}

