import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { ResumeData, WizardState, WorkExperience, Education, Certification, Project, Language, GeneratedResume } from '@/types';
import { STORAGE_KEYS } from '@/types';

interface ResumeStore {
  // State
  resumeData: ResumeData;
  wizardState: WizardState;
  isDirty: boolean;
  lastSaved: Date | null;
  generatedResume: GeneratedResume | null;
  isGenerating: boolean;
  isEditingResume: boolean;
  editedResume: GeneratedResume | null;
  currentResumeId: string | null;
  hasLoadedExistingResume: boolean;

  // Actions
  updateResumeData: (updates: Partial<ResumeData>) => void;
  updateWizardState: (updates: Partial<WizardState>) => void;
  setCurrentStep: (step: number) => void;
  markStepCompleted: (step: number) => void;
  markStepSkipped: (step: number) => void;
  setWizardMode: (mode: 'manual' | 'guided') => void;
  addWorkExperience: (experience: WorkExperience) => void;
  updateWorkExperience: (id: string, updates: Partial<WorkExperience>) => void;
  removeWorkExperience: (id: string) => void;
  addEducation: (education: Education) => void;
  updateEducation: (id: string, updates: Partial<Education>) => void;
  removeEducation: (id: string) => void;
  addCertification: (certification: Certification) => void;
  updateCertification: (id: string, updates: Partial<Certification>) => void;
  removeCertification: (id: string) => void;
  addProject: (project: Project) => void;
  updateProject: (id: string, updates: Partial<Project>) => void;
  removeProject: (id: string) => void;
  addLanguage: (language: Language) => void;
  updateLanguage: (id: string, updates: Partial<Language>) => void;
  removeLanguage: (id: string) => void;
  calculateCharacters: () => number;
  saveToLocalStorage: () => void;
  loadFromLocalStorage: () => void;
  resetResume: () => void;
  setDirty: (dirty: boolean) => void;
  
  // Generated Resume Actions
  setGeneratedResume: (resume: GeneratedResume | null) => void;
  setIsGenerating: (generating: boolean) => void;
  clearGeneratedResume: () => void;
  setCurrentResumeId: (resumeId: string | null) => void;
  
  // Resume Editing Actions
  startEditingResume: () => void;
  stopEditingResume: () => void;
  updateEditedResume: (updates: Partial<GeneratedResume>) => void;
  saveEditedResume: () => void;
  cancelEditingResume: () => void;
  
  // Edit Mode Actions
  setHasLoadedExistingResume: (value: boolean) => void;
}

const initialResumeData: ResumeData = {
  firstName: '',
  lastName: '',
  country: '',
  linkedin: '',
  language: 'es',
  targetLevel: 'mid',
  profession: '',
  tone: 'professional',
  phone: '',
  email: '',
  skillsRaw: [],
  toolsRaw: [],
  experience: [],
  education: [],
  certifications: [],
  projects: [],
  languages: [],
  achievements: [],
  summary: '',
  jobDescription: '',
  completedSteps: [],
  currentStep: 1,
  totalCharacters: 0,
  lastSaved: new Date(),
};

const initialWizardState: WizardState = {
  currentStep: 1,
  completedSteps: [],
  mode: 'guided',
  isCompleted: false,
};

export const useResumeStore = create<ResumeStore>()(
  persist(
    (set, get) => ({
      resumeData: initialResumeData,
      wizardState: initialWizardState,
      isDirty: false,
      lastSaved: null,
      generatedResume: null,
      isGenerating: false,
      isEditingResume: false,
      editedResume: null,
      currentResumeId: null,
      hasLoadedExistingResume: false,

      updateResumeData: (updates) => {
        set((state) => {
          const newResumeData = { ...state.resumeData, ...updates };
          return {
            resumeData: newResumeData,
            isDirty: true,
            lastSaved: new Date(),
          };
        });
      },

      updateWizardState: (updates) => {
        set((state) => ({
          wizardState: { ...state.wizardState, ...updates },
          isDirty: true,
        }));
      },

      setCurrentStep: (step) => {
        set((state) => ({
          wizardState: { ...state.wizardState, currentStep: step },
        }));
      },

      markStepCompleted: (step) => {
        set((state) => {
          const completedSteps = [...state.wizardState.completedSteps];
          if (!completedSteps.includes(step)) {
            completedSteps.push(step);
          }
          return {
            wizardState: {
              ...state.wizardState,
              completedSteps,
            },
            isDirty: true,
          };
        });
      },

      markStepSkipped: (step) => {
        set((state) => {
          const completedSteps = [...state.wizardState.completedSteps];
          if (!completedSteps.includes(step)) {
            completedSteps.push(step);
          }
          return {
            wizardState: {
              ...state.wizardState,
              completedSteps,
            },
            isDirty: true,
          };
        });
      },

      setWizardMode: (mode) => {
        set((state) => ({
          wizardState: { ...state.wizardState, mode },
          isDirty: true,
        }));
      },

      addWorkExperience: (experience) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            experience: [...state.resumeData.experience, experience],
          },
          isDirty: true,
        }));
      },

      updateWorkExperience: (id, updates) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            experience: state.resumeData.experience.map((exp) =>
              exp.id === id ? { ...exp, ...updates } : exp
            ),
          },
          isDirty: true,
        }));
      },

      removeWorkExperience: (id) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            experience: state.resumeData.experience.filter((exp) => exp.id !== id),
          },
          isDirty: true,
        }));
      },

      addEducation: (education) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            education: [...state.resumeData.education, education],
          },
          isDirty: true,
        }));
      },

      updateEducation: (id, updates) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            education: state.resumeData.education.map((edu) =>
              edu.id === id ? { ...edu, ...updates } : edu
            ),
          },
          isDirty: true,
        }));
      },

      removeEducation: (id) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            education: state.resumeData.education.filter((edu) => edu.id !== id),
          },
          isDirty: true,
        }));
      },

      addCertification: (certification) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            certifications: [...state.resumeData.certifications, certification],
          },
          isDirty: true,
        }));
      },

      updateCertification: (id, updates) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            certifications: state.resumeData.certifications.map((cert) =>
              cert.id === id ? { ...cert, ...updates } : cert
            ),
          },
          isDirty: true,
        }));
      },

      removeCertification: (id) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            certifications: state.resumeData.certifications.filter((cert) => cert.id !== id),
          },
          isDirty: true,
        }));
      },

      addProject: (project) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            projects: [...state.resumeData.projects, project],
          },
          isDirty: true,
        }));
      },

      updateProject: (id, updates) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            projects: state.resumeData.projects.map((proj) =>
              proj.id === id ? { ...proj, ...updates } : proj
            ),
          },
          isDirty: true,
        }));
      },

      removeProject: (id) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            projects: state.resumeData.projects.filter((proj) => proj.id !== id),
          },
          isDirty: true,
        }));
      },

      addLanguage: (language) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            languages: [...state.resumeData.languages, language],
          },
          isDirty: true,
        }));
      },

      updateLanguage: (id, updates) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            languages: state.resumeData.languages.map((lang) =>
              lang.id === id ? { ...lang, ...updates } : lang
            ),
          },
          isDirty: true,
        }));
      },

      removeLanguage: (id) => {
        set((state) => ({
          resumeData: {
            ...state.resumeData,
            languages: state.resumeData.languages.filter((lang) => lang.id !== id),
          },
          isDirty: true,
        }));
      },

      calculateCharacters: () => {
        const state = get();
        const data = state.resumeData;
        
        let total = 0;
        total += data.summary.length;
        total += data.jobDescription.length;
        total += data.skillsRaw.join(' ').length;
        total += data.toolsRaw.join(' ').length;
        
        data.experience.forEach(exp => {
          total += exp.title.length + exp.company.length;
          total += exp.achievements.join(' ').length;
          total += exp.responsibilities.join(' ').length;
        });
        
        data.education.forEach(edu => {
          total += edu.institution.length + edu.degree.length + edu.field.length;
        });
        
        data.certifications.forEach(cert => {
          total += cert.name.length + cert.issuer.length;
        });
        
        data.projects.forEach(proj => {
          total += proj.name.length + proj.description.length;
          total += proj.technologies.join(' ').length;
        });
        
        data.languages.forEach(lang => {
          total += lang.name.length;
        });
        
        return total;
      },

      saveToLocalStorage: () => {
        const state = get();
        
        // Don't save to localStorage if we're in edit mode (hasLoadedExistingResume)
        if (state.hasLoadedExistingResume) {
          return;
        }
        
        const dataToSave = {
          resumeData: state.resumeData,
          wizardState: state.wizardState,
          lastSaved: new Date(),
        };
        localStorage.setItem(STORAGE_KEYS.RESUME_WIZARD, JSON.stringify(dataToSave));
        set({ isDirty: false, lastSaved: new Date() });
      },

      loadFromLocalStorage: () => {
        try {
          const saved = localStorage.getItem(STORAGE_KEYS.RESUME_WIZARD);
          if (saved) {
            const data = JSON.parse(saved);
            set({
              resumeData: data.resumeData || initialResumeData,
              wizardState: data.wizardState || initialWizardState,
              lastSaved: data.lastSaved ? new Date(data.lastSaved) : null,
              isDirty: false,
            });
          }
        } catch (error) {
          console.error('Error loading resume data from localStorage:', error);
        }
      },

      resetResume: () => {
        set({
          resumeData: initialResumeData,
          wizardState: initialWizardState,
          isDirty: false,
          lastSaved: null,
          generatedResume: null,
          isGenerating: false,
          currentResumeId: null,
          hasLoadedExistingResume: false,
        });
        localStorage.removeItem(STORAGE_KEYS.RESUME_WIZARD);
        localStorage.removeItem('generated-resume');
      },

      setDirty: (dirty) => set({ isDirty: dirty }),

      // Generated Resume Actions
      setGeneratedResume: (resume) => {
        set({ generatedResume: resume });
        // Tambin guardar en localStorage para persistencia
        if (resume) {
          localStorage.setItem('generated-resume', JSON.stringify(resume));
        } else {
          localStorage.removeItem('generated-resume');
        }
      },

      setIsGenerating: (generating) => set({ isGenerating: generating }),

      clearGeneratedResume: () => {
        set({ generatedResume: null });
        localStorage.removeItem('generated-resume');
      },

      setCurrentResumeId: (resumeId) => set({ currentResumeId: resumeId }),

      // Resume Editing Actions
      startEditingResume: () => {
        const { generatedResume } = get();
        if (generatedResume) {
          set({ 
            isEditingResume: true,
            editedResume: JSON.parse(JSON.stringify(generatedResume)) // Deep copy
          });
        }
      },

      stopEditingResume: () => {
        set({ 
          isEditingResume: false,
          editedResume: null
        });
      },

      updateEditedResume: (updates) => {
        const { editedResume } = get();
        if (editedResume) {
          set({ 
            editedResume: { ...editedResume, ...updates }
          });
        }
      },

      saveEditedResume: () => {
        const { editedResume } = get();
        if (editedResume) {
          set({ 
            generatedResume: editedResume,
            isEditingResume: false,
            editedResume: null
          });
          // Guardar en localStorage
          localStorage.setItem('generated-resume', JSON.stringify(editedResume));
        }
      },

      cancelEditingResume: () => {
        set({ 
          isEditingResume: false,
          editedResume: null
        });
      },

      // Edit Mode Actions
      setHasLoadedExistingResume: (value) => set({ hasLoadedExistingResume: value }),
    }),
    {
      name: 'resume-storage',
      partialize: (state) => ({
        resumeData: state.resumeData,
        wizardState: state.wizardState,
        lastSaved: state.lastSaved,
        generatedResume: state.generatedResume,
        currentResumeId: state.currentResumeId,
      }),
    }
  )
);
