export function WizardPage() {
  const { t } = useTranslation();
  const { wizardState, setCurrentStep, updateResumeData } = useResumeStore();
  const [searchParams] = useSearchParams();
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const loadResume = async () => {
      const resumeId = searchParams.get('resumeId');
      if (resumeId) {
        setIsLoading(true);
        try {
          console.log(' Loading resume with ID:', resumeId);
          const resume = await resumeService.getResume(resumeId);
          console.log(' Resume loaded:', resume);
          console.log(' Resume has resumeData:', !!resume?.resumeData);
          
          if (resume && resume.resumeData) {
            console.log(' Updating resume data with:', resume.resumeData);
            // Pre-popular el store con los datos del resume
            updateResumeData(resume.resumeData);
            toast.success(t('wizard.notifications.loadFromLinkedInSuccess'));
            
            // Redirigir al primer paso del wizard manual
            navigate('/wizard/manual/step-1');
          } else {
            console.error(' Resume or resumeData is missing:', { resume: !!resume, resumeData: !!resume?.resumeData });
            toast.error(t('wizard.notifications.loadError'));
          }
        } catch (error) {
          console.error(' Error loading resume:', error);
          toast.error(t('wizard.notifications.loadError'));
        } finally {
          setIsLoading(false);
        }
      }
    };

    loadResume();
  }, [searchParams, updateResumeData]);

  useEffect(() => {
    // Auto-save every 30 seconds
    const interval = setInterval(() => {
      // Auto-save logic would go here
    }, 30000);

    return () => clearInterval(interval);
  }, []);

  if (isLoading) {
    return (
      <div className="bg-gray-50 min-h-full flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">{t('loading.resume')}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-gray-50 min-h-full">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <Routes>
          {/* Main wizard routes */}
          <Route path="/" element={<ResumeCreationMode />} />
          <Route path="/upload" element={<UploadResume />} />
          <Route path="/linkedin" element={<LinkedInImport />} />
          
          {/* Manual creation wizard */}
          <Route path="/manual" element={<Navigate to="/wizard/manual/step-1" replace />} />
          <Route path="/manual/step-1" element={<ManualWizardStep stepComponent={Step1Profile} />} />
          <Route path="/manual/step-2" element={<ManualWizardStep stepComponent={Step2Skills} />} />
          <Route path="/manual/step-3" element={<ManualWizardStep stepComponent={Step3Experience} />} />
          <Route path="/manual/step-4" element={<ManualWizardStep stepComponent={Step4Education} />} />
          <Route path="/manual/step-5" element={<ManualWizardStep stepComponent={Step5Projects} />} />
          <Route path="/manual/step-6" element={<ManualWizardStep stepComponent={Step6Achievements} />} />
          <Route path="/manual/step-7" element={<ManualWizardStep stepComponent={Step7Summary} />} />
          <Route path="/manual/step-8" element={<ManualWizardStep stepComponent={Step8Preview} />} />
          <Route path="/manual/step-9" element={<ManualWizardStep stepComponent={Step9Download} />} />
        </Routes>
      </div>
    </div>
  );
}
