import { User } from '@/types';

const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:3001/dev';

// Debug: Verificar que la URL de la API se carga correctamente
console.log(' API_URL:', API_URL);
console.log(' All env vars:', import.meta.env);

export interface GoogleLoginResponse {
  token: string;
  user: User;
}

export const authService = {
  async loginWithGoogle(googleToken: string): Promise<GoogleLoginResponse> {
    try {
      const response = await fetch(`${API_URL}/api/auth/google`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ token: googleToken }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Error en la autenticacin');
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error('Error en authService.loginWithGoogle:', error);
      throw error;
    }
  },

  async loginWithLinkedIn(linkedInCode: string): Promise<GoogleLoginResponse> {
    try {
      const response = await fetch(`${API_URL}/api/auth/linkedin`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ code: linkedInCode }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Error en la autenticacin con LinkedIn');
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error('Error en authService.loginWithLinkedIn:', error);
      throw error;
    }
  },

  async validateToken(token: string): Promise<boolean> {
    try {
      const response = await fetch(`${API_URL}/api/auth/validate`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) {
        throw new Error('Token invlido');
      }

      const data = await response.json();
      return data.success;
    } catch (error) {
      console.error('Error en authService.validateToken:', error);
      throw error;
    }
  },

  async getUserTokens(token: string): Promise<number> {
    try {
      const response = await fetch(`${API_URL}/api/user/tokens`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) {
        throw new Error('Failed to fetch tokens');
      }

      const data = await response.json();
      return data.tokens;
    } catch (error) {
      console.error('Error fetching user tokens:', error);
      throw error;
    }
  },

  async getUserFromToken(token: string): Promise<User | null> {
    try {
      // Primero validar el token
      const isValid = await this.validateToken(token);
      if (!isValid) {
        return null;
      }

      // Obtener tokens para verificar que el usuario existe
      const tokens = await this.getUserTokens(token);
      
      // Obtener informacin del usuario desde localStorage
      // (ya que el token es vlido, podemos confiar en los datos locales)
      const userData = localStorage.getItem('user-data');
      if (userData) {
        const user = JSON.parse(userData);
        return { ...user, tokens };
      }

      return null;
    } catch (error) {
      console.error('Error getting user from token:', error);
      return null;
    }
  }
};
