import { SuggestionsResponse, ProfessionSuggestions } from '@/types';

const API_BASE_URL = import.meta.env.VITE_API_URL || 'http://localhost:3001/dev';

class SuggestionService {
  private cache = new Map<string, ProfessionSuggestions>();
  private loadingStates = new Map<string, boolean>();

  private async getAuthToken(): Promise<string | null> {
    const token = localStorage.getItem('auth-token');
    return token;
  }

  private async makeRequest<T>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<T> {
    const token = await this.getAuthToken();
    
    if (!token) {
      throw new Error('Usuario no autenticado. Por favor, inicia sesin para usar las sugerencias de IA.');
    }
    
    const response = await fetch(`${API_BASE_URL}/${endpoint}`, {
      ...options,
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`,
        ...options.headers,
      },
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      
      if (response.status === 401 || response.status === 403) {
        throw new Error('Sesin expirada. Por favor, inicia sesin nuevamente.');
      }
      
      throw new Error(errorData.message || `HTTP error! status: ${response.status}`);
    }

    return response.json();
  }

  /**
   * Obtiene sugerencias para una profesin especfica
   * Incluye cache en memoria para evitar llamadas repetidas
   */
  async getSuggestions(profession: string): Promise<ProfessionSuggestions> {
    // Normalizar profesin para el cache
    const normalizedProfession = profession.toLowerCase().trim();
    
    // Verificar cache primero
    if (this.cache.has(normalizedProfession)) {
      return this.cache.get(normalizedProfession)!;
    }

    // Verificar si ya est cargando
    if (this.loadingStates.get(normalizedProfession)) {
      // Esperar a que termine la carga actual
      return new Promise((resolve, reject) => {
        const checkLoading = () => {
          if (!this.loadingStates.get(normalizedProfession)) {
            if (this.cache.has(normalizedProfession)) {
              resolve(this.cache.get(normalizedProfession)!);
            } else {
              reject(new Error('Failed to load suggestions'));
            }
          } else {
            setTimeout(checkLoading, 100);
          }
        };
        checkLoading();
      });
    }

    try {
      // Marcar como cargando
      this.loadingStates.set(normalizedProfession, true);

      // Codificar la profesin para la URL
      const encodedProfession = encodeURIComponent(profession);
      
      const response = await this.makeRequest<SuggestionsResponse>(
        `api/suggestions/${encodedProfession}`,
        {
          method: 'GET',
        }
      );

      if (!response.success || !response.data) {
        throw new Error(response.error || 'Failed to get suggestions');
      }

      // Guardar en cache
      this.cache.set(normalizedProfession, response.data);

      return response.data;
    } catch (error) {
      console.error('Error getting suggestions:', error);
      throw error;
    } finally {
      // Marcar como no cargando
      this.loadingStates.set(normalizedProfession, false);
    }
  }

  /**
   * Obtiene sugerencias de habilidades para una profesin especfica
   */
  async getSkillsSuggestions(profession: string): Promise<string[]> {
    const normalizedProfession = profession.toLowerCase().trim();
    const language = this.getCurrentLanguage();
    
    // Verificar cache especfico para skills con idioma
    const cacheKey = `${normalizedProfession}-${language}-skills`;
    if (this.cache.has(cacheKey)) {
      return this.cache.get(cacheKey)!.skills;
    }

    if (this.loadingStates.get(cacheKey)) {
      throw new Error('Skills suggestions are already being loaded for this profession.');
    }

    this.loadingStates.set(cacheKey, true);

    try {
      const encodedProfession = encodeURIComponent(profession);
      const response = await this.makeRequest<SuggestionsResponse>(
        `api/suggestions/${encodedProfession}?type=skills&language=${language}`
      );

      if (!response.success || !response.data?.skills) {
        throw new Error(response.error || 'Failed to fetch skills suggestions');
      }

      // Cache with language-specific key
      this.cache.set(cacheKey, { skills: response.data.skills, tools: [] });

      return response.data.skills;
    } catch (error) {
      console.error('Error fetching skills suggestions:', error);
      throw error;
    } finally {
      this.loadingStates.set(cacheKey, false);
    }
  }

  /**
   * Obtiene sugerencias de herramientas para una profesin especfica
   */
  async getToolsSuggestions(profession: string): Promise<string[]> {
    const normalizedProfession = profession.toLowerCase().trim();
    const language = this.getCurrentLanguage();
    
    // Verificar cache especfico para tools con idioma
    const cacheKey = `${normalizedProfession}-${language}-tools`;
    if (this.cache.has(cacheKey)) {
      return this.cache.get(cacheKey)!.tools;
    }

    if (this.loadingStates.get(cacheKey)) {
      throw new Error('Tools suggestions are already being loaded for this profession.');
    }

    this.loadingStates.set(cacheKey, true);

    try {
      const encodedProfession = encodeURIComponent(profession);
      const response = await this.makeRequest<SuggestionsResponse>(
        `api/suggestions/${encodedProfession}?type=tools&language=${language}`
      );

      if (!response.success || !response.data?.tools) {
        throw new Error(response.error || 'Failed to fetch tools suggestions');
      }

      // Cache with language-specific key
      this.cache.set(cacheKey, { skills: [], tools: response.data.tools });

      return response.data.tools;
    } catch (error) {
      console.error('Error fetching tools suggestions:', error);
      throw error;
    } finally {
      this.loadingStates.set(cacheKey, false);
    }
  }

  /**
   * Verifica si hay sugerencias en cache para una profesin
   */
  hasCachedSuggestions(profession: string): boolean {
    const normalizedProfession = profession.toLowerCase().trim();
    return this.cache.has(normalizedProfession);
  }

  /**
   * Verifica si una profesin est siendo cargada actualmente
   */
  isLoading(profession: string): boolean {
    const normalizedProfession = profession.toLowerCase().trim();
    return this.loadingStates.get(normalizedProfession) || false;
  }

  /**
   * Verifica si las habilidades estn siendo cargadas para una profesin
   */
  isLoadingSkills(profession: string): boolean {
    const normalizedProfession = profession.toLowerCase().trim();
    const language = this.getCurrentLanguage();
    return this.loadingStates.get(`${normalizedProfession}-${language}-skills`) || false;
  }

  /**
   * Verifica si las herramientas estn siendo cargadas para una profesin
   */
  isLoadingTools(profession: string): boolean {
    const normalizedProfession = profession.toLowerCase().trim();
    const language = this.getCurrentLanguage();
    return this.loadingStates.get(`${normalizedProfession}-${language}-tools`) || false;
  }

  /**
   * Obtiene el idioma actual del usuario
   */
  private getCurrentLanguage(): string {
    // Get language from i18next
    return localStorage.getItem('i18nextLng') || 'es';
  }

  /**
   * Limpia el cache de sugerencias
   */
  clearCache(): void {
    this.cache.clear();
    this.loadingStates.clear();
  }

  /**
   * Limpia el cache para una profesin especfica
   */
  clearCacheForProfession(profession: string): void {
    const normalizedProfession = profession.toLowerCase().trim();
    this.cache.delete(normalizedProfession);
    this.loadingStates.delete(normalizedProfession);
  }
}

export const suggestionService = new SuggestionService();
